<template>
  <div :id="id" :style="{ width: '100%', height: '100%' }"></div>
</template>
<script>
import * as echarts from 'echarts';
export default {
  name: "Branch",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        if (newVal) {
          this.drawLine();
        }
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      const myChart = echarts.init(document.getElementById(this.id));

      const option = {
        tooltip: {
          trigger: 'item'
        },
        series: [
          {
            name: '分支机构',
            type: 'pie',
            radius: ['80%', '70%'],
            avoidLabelOverlap: false,
            padAngle: 5,
            labelLine: {
              show: true
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 40,
                fontWeight: 'bold'
              }
            },
            data: [
              { value: 575, name: '机构总数' },
              { value: 234, name: '单组建立党组织的' },
              { value: 116, name: '党组织数' },
            ].sort(function (a, b) {
              return a.value - b.value;
            }),
          }
        ]

      };
      myChart.setOption(option);

      window.addEventListener('resize', () => {
        myChart.resize();
      });
    }
  }
};
</script>