<template>
  <div :id="id" :style="{ width: '100%', height: '100%' }"></div>
</template>
  
  <script>
import * as echarts from 'echarts';
import imageUrl from '../../assets/img/Index/fa-en-jg-cent.png'
// import ringImageUrl from '../../assets/img/Index/large-ring.png'; // 环形图片资源路径
export default {
  name: "Pc2FbEcharts",
  props: {
    id: {
      type: String,
      default: "myChart",
    },
    dataList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      imageUrl,
      list1: [], // 用于存储处理后的数据
      list2: []  // 用于存储处理后的数据
    };
  },
  watch: {
    dataList: {
      handler(newVal) {
        this.list1 = newVal.map(item => item.value1);
        this.list2 = newVal.map(item => item.value2);
        this.list1 = this.list1.slice(0, 6); // 使用 slice 而不是 splice
        this.list2 = this.list2.slice(0, 6); // 使用 slice 而不是 splice
        this.drawLine();
      },
      deep: true
    }
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      const myChart = echarts.init(document.getElementById(this.id));

      // 定义颜色配置
      const colorMap = {
        'Search Engine': '#00FF00',
        'Direct': {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 0,
          colorStops: [
            { offset: 0, color: 'rgba(114,250,255,0.91)' }, // 左边颜色
            { offset: 1, color: 'rgba(0,126,255,0.55)' }   // 右边颜色
          ],
          global: false
        },
        'Email': {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 1,
          colorStops: [
            { offset: 0, color: 'rgba(0,228,255,0.85)' }, // 上边颜色
            { offset: 1, color: 'rgba(0,132,255,0.64)' }  // 下边颜色
          ],
          global: false
        }
      };

      // 创建一个映射，用来快速查找每个name对应的value
      const valueMap = {};
      if (this.dataList.length) {
        this.dataList.forEach(item => {
          valueMap[item.name] = item.value1;
        });
      }

      const option = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: '61.4%', // 调整图例的左边距
          top: 'center', // 置中对齐
          align: 'left', // 对齐方式
          icon: 'rect', // 设置图例的图标为矩形
          itemWidth: 14, // 设置图例项宽度
          itemHeight: 14, // 设置图例项高度
          itemGap: 20, // 设置图例项之间的间距
          textStyle: {
            color: 'white' // 设置图例文本颜色为白色
          },
          formatter: (name) => {
            const value = valueMap[name] || 0; // 获取对应name的value，如果没有则默认为0
            return `${name}\n${value}`;
          }
        },
        series: [
          {
            name: '法人机构',
            type: 'pie',
            radius: ['45%', '60%'],
            center: ['25%', '50%'], // 将环图中心移动到中间
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: 'center'
            },
            labelLine: {
              show: false
            },
            data: this.dataList.length ? this.dataList.map(item => ({
              value: item.value1,
              name: item.name,
              itemStyle: {
                color: colorMap[item.name] || '#ccc' // 默认颜色
              }
            })) : [
              { value: 1048, name: 'Search Engine', itemStyle: { color: colorMap['Search Engine'] } },
              { value: 735, name: 'Direct', itemStyle: { color: colorMap['Direct'] } },
              { value: 580, name: 'Email', itemStyle: { color: colorMap['Email'] } },
            ]
          }
        ],
        graphic: {
          elements: [
            {
              type: 'image',
              style: {
                image: this.imageUrl, // 图片的URL
                width: 100, // 图片宽度
                height: 100 // 图片高度
              },
              left: 50, // 水平居中
              top: 'center', // 垂直居中
            }
          ]
        }
      };

      // 绘制图表
      myChart.setOption(option);

      // 监听窗口大小变化，自动调整图表大小
      window.addEventListener('resize', () => {
        myChart.resize();
      });
    }
  }
};
  </script>
  
  <style scoped>
/* 根据需要添加样式 */
</style>